.hero-bg {
    background-image: url(../../assets/backgroundBlur1.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    /* aspect-ratio: 16/9; */
}

.hero-content {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
    text-align: center;
    width: 100vw;
    height: 100vh;
}

.logo {
    filter: drop-shadow(0px 4px 40px rgba(0, 0, 0, 0.75));
}
