.mainIdea.container {
    margin-top: 150px;
}
.mainIdea.background {
    position: relative;
    background-image: url('../../assets/backgroundBlur3.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100vw;
}

.mainIdea h2 {
    text-align: center;
    color: #828282;
    font-size: 64px;
    line-height: 96px;
    font-weight: 800;
    letter-spacing: 0.05em;
}

.mainIdea.content {
    font-size: 36px;
    line-height: 39px;
    font-weight: 600;
    letter-spacing: 0.1em;
    color: white;
    padding: 50px 400px;
    text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.51);
    text-align: center;
}