.group.container {
    margin-top: 150px;
    padding-bottom: 100px;
    background: #F2F2F280;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0px;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.group h2 {
    text-align: center;
    color: #828282;
    font-size: 64px;
    line-height: 96px;
    font-weight: 800;
    letter-spacing: 0.05em;
}

.group p {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: 0.16em;
    color: #000000;
}